.catalog {
  background-color: $color-black-pearl;
  color: $color-default-white;
}

.catalog__container {
  padding: 0 5px 140px;

  @include vp-1024 {
    padding: 0 5px 73px;
  }

  @include vp-768 {
    padding: 0 5px 86px 5px;
  }
}

.catalog__container h2 {
  font-family: $ff-placeholder-second;
  font-size: 36px;
  font-weight: 400;
  line-height: 51px;
  margin: 0;
  margin-bottom: 53px;
  padding-top: 66px;
  max-width: 900px;

  @include vp-1024 {
    margin-bottom: 39px;
    padding-top: 54px;
  }

  @include vp-768 {
    max-width: 245px;
    font-size: 28px;
    line-height: 39px;
    margin-bottom: 29px;
    padding-top: 48px;
  }
}

.catalog__container h2>span:first-child {
  @include vp-768 {
    display: none;
  }
}

.catalog__container h2>span:last-child {
  display: none;

  @include vp-768 {
    display: block;
  }
}

.catalog__list {
  margin: 0;
  padding: 0;
  list-style: none;

  // display: grid;
  // grid-template-columns: repeat(3, 1fr);

  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  column-gap: 85px;
  row-gap: 30px;

  @include vp-768 {
    gap: 0;
  }
}

.catalog__item {
  @include vp-768 {
    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}
